.nav__main-link {
  margin-right: auto;
}
@media (min-width: 660px) {
  .nav__main-link {
    margin-top: -11px;
  }
}
@media (min-width: 1200px) {
  .nav__main-link {
    margin-right: 175px;
  }
}

.nav__main-link:hover .nav__logo {
  filter: brightness(0) saturate(100%) invert(35%) sepia(73%) saturate(7406%) hue-rotate(332deg) brightness(88%) contrast(86%);
}

.nav__main-link:active .nav__logo {
  filter: brightness(0) saturate(100%) invert(93%) sepia(100%) saturate(2%) hue-rotate(55deg) brightness(108%) contrast(100%);
  opacity: 0.3;
}

.nav__main-link--active:hover .nav__logo {
  filter: none;
}

.nav__main-link--active:active .nav__logo {
  filter: none;
  opacity: 1;
}

@media (min-width: 660px) {
  .nav__logo {
    width: 145px;
    height: 40px;
  }
}

.nav__button {
  position: relative;
  top: -4px;
  width: 50px;
  height: 24px;
  border: none;
  background-color: transparent;
}
.nav__button::before {
  content: "";
  position: absolute;
  top: 11px;
  right: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 -10px 0 0 #ffffff, 0 10px 0 0 #ffffff;
}
@media (min-width: 660px) {
  .nav__button {
    top: auto;
  }
}
@media (min-width: 1200px) {
  .nav__button {
    display: none;
  }
}

.nav__button:hover::before {
  background-color: #d22856;
  box-shadow: 0 -10px 0 0 #d22856, 0 10px 0 0 #d22856;
}

.nav__button--active {
  top: auto;
}
.nav__button--active::before {
  top: 10px;
  right: -3px;
  width: 29px;
  height: 3px;
  transform: rotate(45deg);
  box-shadow: none;
}
.nav__button--active::after {
  content: "";
  position: absolute;
  top: 10px;
  right: -3px;
  width: 29px;
  height: 3px;
  background-color: #ffffff;
  transform: rotate(-45deg);
  box-shadow: none;
}

.nav__button--active:hover::before {
  background-color: #d22856;
  box-shadow: none;
}
.nav__button--active:hover::after {
  background-color: #d22856;
}

.nav__list {
  position: absolute;
  top: 73px;
  right: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  display: none;
  list-style-type: none;
  z-index: 1;
}
.nav__list--opened {
  display: block;
}
@media (min-width: 660px) {
  .nav__list {
    top: 110px;
  }
}
@media (min-width: 1200px) {
  .nav__list {
    position: static;
    display: flex;
    flex-wrap: wrap;
  }
}

.nav__item {
  background-color: #283645;
  text-align: center;
  box-shadow: 0 -1px 0 0 #000000, 0 1px 0 0 rgba(255, 255, 255, 0.1490196078) inset;
}
@media (min-width: 1200px) {
  .nav__item {
    background-color: transparent;
    box-shadow: none;
  }
  .nav__item:not(:last-child) {
    margin-right: 79px;
  }
}

@media (min-width: 1200px) {
  .nav__item--active .nav__link {
    position: relative;
  }
  .nav__item--active .nav__link:hover {
    color: #ffffff;
  }
  .nav__item--active .nav__link:active {
    color: #ffffff;
  }
  .nav__item--active .nav__link::before {
    content: "";
    position: absolute;
    bottom: 10px;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
  }
}

.nav__link {
  min-height: 63px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}
.nav__link:hover {
  color: #d22856;
}
.nav__link:active {
  color: rgba(210, 40, 86, 0.3);
}
@media (min-width: 1200px) {
  .nav__link {
    text-transform: none;
    line-height: 18px;
    font-weight: 400;
  }
  .nav__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

